<template>
  <div>
    <div>HEllo!</div>
    <button @click="sendMessage(3)">code 3!</button>
    <button @click="sendMessage(2)">code 2!</button>
  </div>
</template>
 3
<script>
export default {
  data() {
    return {
      ws: null
    };
  },
  methods: {
    sendMessage(n) {
      if (this.ws !== null) {
        let serverMsg = {
          client: "sans",
          code: n,
          gameState: {}
        };
        this.ws.send(JSON.stringify(serverMsg));
      }
    }
  },
  created() {
    this.ws = new WebSocket("wss://smasharena.herokuapp.com/arena/sans" );
    this.ws.onopen = () => {
      console.log("[open] Connection established");
    };
    this.ws.onmessage = (e) =>{
      console.log(e.data)
    }
  }
};
</script>